<script>
	import emotion from '@/components/bkhumor-emoji/index.vue';

	export default {

		created() {

		},
		components:{
			emotion
		},
		onShow() {

		},
		data() {
			return {
				title: '18254213105',
				bt_show: false,
				url:'../../static/bkhumor-emoji/',
				img:'11.gif',
				chat_txt: '',
				changyong:'',
				
				changyong_arry: ['你好,有什么可以帮助到您？', '客服的上班时间是早晨八点到晚上八点', '好的，有什么事情可以找客服帮您疑难解答'],
				isShowEmj: false,
				id:'100004',
				message:[
					{
						addtime: 1591171851,
						content: "就是读书期间，在外面打架被判刑了可以保出来的?",
						flag: 1,
						messageid: 173,
						msgtype: 1,
						nickname_from: "1234",
						nickname_to: "会员100005",
						photourl_form: "user/20200525/159037945424676.png",
						photourl_to: "user/20200529/159074444286391.png",
						read: 1,
						userid_from: 100004,
						userid_to: 100005,
					},
					{   addtime: 1591171761,
						content: "[em_12]45646",
						flag: 1,
						messageid: 172,
						msgtype: 4,
						nickname_from: "会员100005",
						nickname_to: "1234",
						photourl_form: "user/20200529/159074444286391.png",
						photourl_to: "user/20200525/159037945424676.png",
						read: 1,
						userid_from: 100005,
						userid_to: 100004,
					},
					{
						addtime: 1591171851,
						content: "对以后的就业有没有什么影响？",
						flag: 1,
						messageid: 173,
						msgtype: 1,
						nickname_from: "1234",
						nickname_to: "会员100005",
						photourl_form: "user/20200525/159037945424676.png",
						photourl_to: "user/20200529/159074444286391.png",
						read: 1,
						userid_from: 100004,
						userid_to: 100005,
					},
					
					{   addtime: 1591171761,
						content: "你好！按照你说的这个情况来看，如果是行政拘留的话，可能对他的升迁有影响，这个一般情况下不会上征信，可以考公务员，对孩子没有多大影响。万一如果是刑事拘留的话，就是有案底了。",
						flag: 1,
						messageid: 172,
						msgtype: 1,
						nickname_from: "会员100005",
						nickname_to: "1234",
						photourl_form: "user/20200529/159074444286391.png",
						photourl_to: "user/20200525/159037945424676.png",
						read: 1,
						userid_from: 100005,
						userid_to: 100004,
					},
				]
			}
		},
		//下拉刷新
		onPullDownRefresh: function() {
			// this.getList();

			setTimeout(function() {
				uni.showToast({
					title: '没有更多内容了',
					duration: 2000,
					icon: "none"
				});
				uni.stopPullDownRefresh();
			}, 1000);
		},
		// stopPullDownRefresh:function(){

		// },
		methods: {
			//上拉加载
			onReachBottom() {
				// uni.showToast({
				// 	title: '没有更多内容了',
				// 	duration: 2000,
				// 	icon: "none"
				// });
			},
			navigateBack() {
				uni.navigateBack()
			},
	dianzan(){
		uni.showToast({
			title: '点赞成功',
			duration: 2000,
			
		});
	},
	handleEmj(i) {
		console.log(i)
		if(i == '[em_98]') {
			//匹配最后一个表情符号并删除。
			this.chat_txt = this.chat_txt.replace(/(\[[^\]]+\]|[\s\S])$/, '');
		} else {
			this.chat_txt += i;
		}
	},
	showEmj() {
		let bool = !this.isShowEmj;
		if(bool) {
			this.emojiIcon = 'cuIcon-keyboard';
			this.bt_show=false
		} else {
			this.emojiIcon = 'cuIcon-emoji';
		}
		
		this.isShowEmj = bool;
		this.$emit('show')
	},
	
			jia() {
				this.bt_show = !this.bt_show
				this.isShowEmj=false
			},

			send() {
				console.log(this.chat_txt)
				
				var data={
					content:''+this.chat_txt,
					userid_from:''+this.id
				}
				
				this.message.push(data)
				this.chat_txt=''
				
			},
			call(){
				uni.makePhoneCall({
				 	// 手机号
				    phoneNumber: '110', 
					// 成功回调
					success: (res) => {
						console.log('调用成功!')	
					},
					// 失败回调
					fail: (res) => {
						console.log('调用失败!')
					}
					
				  });
			},
	
			changyong_change(data) {
				this.changyong = this.changyong_arry[data.detail.value]
				console.log(this.changyong)
			},
			pingjia(){
				uni.navigateTo({
					url:'pingjia'
				})
			},
			tan_hide(){
				this.isShowEmj=false
				this.bt_show=false
			}
		}
	}
</script>

<style>
