		padding-left: 10rpx;
		box-sizing: border-box;
	}

	.chat_bottom_bottom {
		background-color: #fafafa;
		display: flex;
		justify-content: space-between;
		padding: 40rpx 0;
	}

	.chat_bt_item {
		text-align: center;
		width: 33.33%;
	}

	.chat_bt_item image {
		width: 126rpx;
		height: 126rpx;
